<template>
  <div class="main-box detail">
		<div class="swiper-container detail">
	    <div class="swiper-wrapper">
	    		
	        <div class="swiper-slide" 
	        	v-for="item in goodData.imgs">
	        	<img :src="item"/>
	        </div>
	    </div>
	    <!-- Add Pagination -->
	    <div class="swiper-pagination"></div>
    </div>
    <div class="info-box">
			<div class="area">
				<div class="prices flex-center align-bottom">
					<p class="price theme-txt" style="color:#FF4965">
						<i class='price-new'>¥{{groupPrice}}</i><i class="price-old">¥{{pageData.origin_price/100}}</i>
						<span class="sale-tag" style="background:#FF4965">拼团价</span>
						<span class="sale-tag" style="background:#FF4965">包邮</span>
					</p>
				</div>
				<div class="group">
					<span class="J_countdown">{{runTime}}</span>
					<span class="num">{{pageData.sold_num}}人已团</span>
				</div>
			</div>
			<h1 class="title">{{pageData.title}}</h1>
			<div class="promise space-box">
				<span><i class="iconfont">&#xe7ce;</i>正品保证</span>
				<span><i class="iconfont">&#xe7ce;</i>全场包邮</span>
				<span><i class="iconfont">&#xe7ce;</i>24h发货</span>
				<span><i class="iconfont">&#xe7ce;</i>售后补贴</span>
			</div>
			<img v-for="(src,i) of pageData.detail_imgs" :src="src" :class='{"space-box":i==0}'/>
    </div>
   
    <footer>
			<a href="#/mine" class="none son-btn">
				<span class="iconfont">&#xe600;</span>
				<span>收藏</span>
			</a>
			<h2 @click="addCar" class="son-btn">
				<span class="iconfont">&#xe8db;</span>
				<span >购物车</span>
			</h2>
			<div>
				<p>¥{{price}}</p>
				<p>单独购买</p>
			</div>
			<div>
				<p>¥{{groupPrice}}</p>
				<p>{{require_num}}人团</p>
			</div>
		</footer>
		<go-top home="home"></go-top>
  </div>
</template>
<script>
import {mapState,mapActions} from 'vuex'
import jsonp from 'jsonp'
import axios from 'axios'
import GoTop from './GoTop'


export default {
  name: 'detail',
  swiper:null,
  data () {
    return {
    	pageData:[],//页面信息
    	goodData:[],//商品信息----大图-尺码等等
    	price:'',
    	groupPrice:'',
    	require_num:'',
    	rate:[],
    	runTime:'',
    	url:'http://sapi.beibei.com/item/',
    	src:''
    }
  },
  computed:{
  	...mapState(['goodsId'])
  },
  methods:{
  	...mapActions(['addGoods']),
  	addCar(){
  		var {goodsId,price,groupPrice,src,tit}=this
  		var obj={goodsId,price,groupPrice,src,tit,num:1,check:true}
  		this.addGoods([obj,1])
  	}
  },
  created(){
  	//获取页面详情信息
  	var that=this;
		jsonp(this.url+'detail/new/'+this.goodsId+'.html',{
			prefix:'jsonp',
			name:'BeibeiItemDetailNewGet'
		},function(err,res){
				that.pageData=res;
				that.price=res.price/100;
				that.groupPrice=res.pintuan_area_info.group_price/100;
				that.require_num=res.pintuan_area_info.require_num;
				that.src=res.main_img;
				that.tit=res.title;
				console.log(res)
				var time=new Date(res.gmt_end*1000);
  			var iNow,t,timer;
  			timer=setInterval(fnTime,1000);
				function fnTime(){
					iNow=Math.floor(+new Date());
					t=Math.floor((time-iNow)/1000);
					if (t >= 0){	
						that.runTime=Math.floor(t/86400)+'天'+toTwo(Math.floor(t%86400/3600))+'时'
						+toTwo(Math.floor(t%86400%3600/60))+'分'+toTwo(t%60)+'秒后结束';
					}else{
						that.runTime="抢购已结束"
						clearInterval( timer );
					}
				}
				function toTwo (n) {
					return n<10 ? '0'+n : n;
				}
		})
//	//获取评价信息----有的商品可能没有
//		jsonp(this.url+'rate/0-'+this.goodsId+'-1-10.html',{
//			prefix:'jsonp',
//			name:'BeibeiItemRateGet'+this.goodsId
//			},function(err,res){
//				console.log('详情',res)
//		})
  	axios({//商品详情大图-尺码等等
			url: '/mroute.html',		
			params:{
					method:'beibei.item.stock.get',
					iid:this.goodsId,
					pintuan_type:''
				}
			}).then((res)=>{
				that.goodData=res.data;
			});
  },
  watch:{
  	goodData(newVal,oldVal){
  		if(newVal){
  			var that=this;
  			setTimeout(()=>{
	  			that.$options.swiper=new Swiper('.swiper-container',{
	       		pagination: '.swiper-pagination',
	        	loop:true
	    		});	
  			},0)
  		}
  	}
  },
  components:{
  	GoTop
  }
}
</script>

<style lang="scss">
.main-box.detail{
	margin-bottom:2.048rem;
 	width:100%;
 	position:relative;
	overflow:hidden;
	footer{
    height: 2.048rem;
    width:100%;border-top:1px solid #CCC;
    position: fixed;
    z-index: 99;
    bottom: 0;
    left: 0;
    text-align: center;
    font-weight: 700;
    background:#fff;
    display:flex;
    .son-btn{
    	display:flex;
    	width:16%;flex-wrap:wrap;
    	border-left:1px solid #CCC;
    	&.none{border:none}
    	span{
    		display:block;width:100%;
    		font-size:28px;
    		&.iconfont{
    			font-size:36px;
    		}
    	}
    }
    div{
    	width:34%;color:#fff;
    	padding-top:5px;
    	font:600 28px/1.3em arial;
    	&:first-of-type{
    		background:rgba(255,73,100,.6);
    	}
    	&:last-of-type{
    		background:#ff4965;
    	}
    }
	}
}
.detail{
	.swiper-slide{
		width:100vw;height:100vw;
	}
 	.swiper-pagination{
 			.swiper-pagination-bullet{
 				width: .34133333rem;
 	   	height: .34133333rem;
 	   	&.swiper-pagination-bullet-active{
 	   		background:#ff4965;
 	   	}
 		}
 	} 	
}
.info-box{
	>*{background:#fff;}
	.area{	
		padding: .3rem 0 0 .512rem;
		.price{
			span{
				display:inline-block;
				color:#fff;padding:0 10px;
				margin-left:10px;border-radius:5px;
				font-size:20px;
			}		
		}
		.price-new{
			font-weight:600;color:#FF4965;font-size:40px;
		}
		.price-old{
			margin-left: .256rem;
	    display: inline-block;
	    font-size:26px;
	    font-weight:500;
	    text-decoration: line-through;
	    color: #8f8f8f;
		}
		.group{
	    color: #8f8f8f;
	    font-size:24px;
	    line-height:1.5em;
	    display:flex;
	    padding-right:20px;
	    justify-content:space-between;
		}
	}
	.title{
    padding-right: .512rem;
    padding-left: .512rem;
    font-size:30px;
    color: #3d3d3d;
    font-weight: 700;
	}
	.space-box{
		margin-top:10px;
	}
	.promise{
		font-size:24px;
		line-height:2.5em;
		justify-content:space-around;
    display: flex;
    .iconfont{
    	color:#FF4965;
    	font-size:20px;
    }
	}
}
 
 
</style>



