<template>
	<div class="ss-top">
		<search-top></search-top>
		
		<div class="search-one" v-for="item in arr">
			<div class="search-header">{{item.category_name}}</div>
			<div class="search-nav">								
				<div class="header-item" v-for="i in item.subdivision_categorys">    
					<router-link :to="'/searchlist/'+i.data">	
						<img :src="i.img"/> 
						<p class="header-title">{{i.title}}</p>      
					</router-link>
				</div>				
			</div>
		</div>
	
	</div>
</template>

<script>
	import Vue from 'vue'
	import axios from 'axios'
	import jsonp from 'jsonp'
	import SearchTop from './SearchTop'
	export default {
		name :"search",
		data(){
			return {
				arr:[],
				xqarr:[]
			}			
		},
		methods:{
			
		},
		mounted(){			
			var that = this
			jsonp("http://sapi.beibei.com/item/category-1.html",{
				prefix:"jsonp",
				name:"BeibeiItemCategoryGet"
				//Request URL是 jsonp格式的callback霍地凹函数的名字要写在参数name里面
			},function(err,res){
				that.arr = res.main_categorys
//				console.log(that.arr[0].subdivision_categorys)
			})
		},
		components:{
			SearchTop
		}
	}
</script>

<style scoped lang="scss">
	.ss-top{
		  width: 100%;
	    overflow: hidden;
	}
	
	.search-header{
	    height: 1.38666667rem;
	    padding-left: .512rem;
	    font-size: .64rem;
	    line-height: 1.38666667rem;
	    color: #333;
	    background-color: #fafafa
	}
	.search-nav{
		border-top: solid 1px #e6e6e6;
	    border-bottom: solid 1px #e6e6e6;
	    background-color: #fff;
	    display: flex;
	    /*flex-flow: row;*/
	  	align-items: auto ;
	  	flex-direction: row;
	  	flex-wrap: wrap;
	    
	}
	.header-item{
		justify-content: center;
	    width: 25%;
	    height: 4.48rem;
	    a{
	    	display:block;
	    	width:100%;
	    	height:100%;
	    }
	}
	.header-item img{
		display: block;
	    width: 2.56rem;overflow:hidden;
	    margin: .55466667rem auto .256rem;
	}
	.header-title{
	    text-align: center;
	    color: #323232;
	    font-size: .6rem;
	    line-height: .59733333rem;
	    white-space: nowrap;
	    overflow: hidden;
	    text-overflow: ellipsis;
	}
	.search-one{
		width: 100%;height: 100%;
	}
	

</style>


