<template>
	<div id="contain">
		
		<section class="adver">
			<div class="title">
				<img src="http://h0.hucdn.com/open/201720/de26702fa51114d2_84x84.png"/>
				<span class="sontitle">今日最热</span>
			</div>
			<div class="slide">
				<div class="slide-item" v-for='x in hot' @click="goDetail(x.iid)">
					<div class="img-container">
						<img :src="x.img"/>
						<img class="pos" src="http://h0.hucdn.com/open/201720/7e3fa907598e3e2d_122x122.png"/>
					</div>
					<p class="introduce">{{x.title}}</p>
					<p class="price"><span class="lit">￥{{x.group_price/100}}</span><span class="buying">{{x.buying_info}}</span></p>
				</div>
			</div>
		</section>
		
		<section class="patient">
			<div class="title">
				<img src="http://h0.hucdn.com/open/201720/49ca1f7e1aae456c_84x84.png"/>
				<span class="sontitle">精选好货</span>
				<p>今日必拼，每日早上9点更新</p>
				<a v-for="info in today"><img :src="info.img"/><p>{{info.title}}</p></a>
			</div>
		</section>
		
		<section class="production-list">
			<ul class="list-container" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
				<li class="each" v-for="x in items" @click="goDetail(x.iid)">
					<img :src="x.img" />
					<p class="pro-name">{{x.title}}</p>
					<p class="oldprice"><span>￥{{x.origin_price/100}}</span></p>
					<p class="nowprice"><span>￥{{x.group_price/100}}</span><span class="in">{{x.buying_info}}</span></p>
				</li>
			</ul>
		</section>
		
	</div>
</template>

<script>
	import {mapActions} from 'vuex'
	import jsonp from 'jsonp'

	export default {
		name:'son2',
		data(){
			return {
				today:[],
				items:[],
				hot:[],
				order:1,
				keyword:this.$route.params.key
			}
		},
		watch:{
			$route(newkey,oldkey){
				if(newkey){
					this.today = []
					this.items = []
					this.hot = []
				}
				this.keyword = newkey.params.key
				this.getTodayMust(this.keyword)
				this.getInfo(this.order,this.keyword)
			}
		},
		methods:{
			loadMore() {
				this.getInfo(this.order,this.keyword)
				this.loading = true;
			},
			getTodayMust(keyword){
				var that = this;
				jsonp('http://sapi.beibei.com/resource/ads-android-0--186_212_214_343_344_373_384-4.8.0-.html',{
					prefix:'jsonp',
					name:'ads'
				},function(err,res){
					that.today = that.today.concat(res.fightgroup_cat_ads[keyword])					
				})
			},
			
			getInfo(num,keys) {
				var that = this
				jsonp('http://sapi.beibei.com/item/fightgroup/'+num+'-40-today_group-'+keys+'-0.html', {
					prefix: 'jsonp',
					name: 'BeibeiFightgroupItemGet'
				}, function(err, res) {
					if(res&&res.hot_datas){
						that.hot = res.hot_datas.hot_items
					}else{
						return
					}
					that.items = that.items.concat(res.fightgroup_items)
					that.loading = false
					that.order++
				})
			},
			...mapActions(['setGoodsId']),
			goDetail(iid){
				location.hash='detail'
				this.setGoodsId(iid)
			}
		},
		beforeMount(){
			this.getTodayMust(this.keyword)
			this.getInfo(this.order,this.keyword)	
		}
	}
</script>

<style scoped lang="scss">

	.patient{
		padding-bottom: .512rem;
	    margin-bottom: .21333333rem;
	    background-color: #fff;
	    -webkit-text-size-adjust: none;
	}
	
		.title{
			display: inline-block;
			width: 100%;
			height: 1.19466667rem;
		    padding: .42666667rem 0;
		    background-color: #fff;
		    text-align: center;
		    .sontitle{
		    	margin-left: .128rem;
			    line-height: 1.19466667rem;
			    font-size: 32px;
			    color: #3d3d3d;
		    }
		    p{
		    	color: #8f8f8f;
		    	font-size:24px;
		    }
		    img{
		    	display: inline-block;
			    width: 1.19466667rem;
			    height: 1.19466667rem;
			    vertical-align: middle;
		    }
		    a{
		    	display: inline-block;
		    	img{
		    		padding: .512rem .93333333rem .34133333rem;
				    width: 2.13333333rem;
				    height: 2.13333333rem;
		    	}
		    	p{
	    		    line-height: 1;
				    text-align: center;
				    color: #3d3d3d;
				    font-size: 24px;
				    overflow: hidden;
				    white-space: nowrap;
				    word-break: break-all;
				    text-overflow: ellipsis;
		    	}
		    }
		}
	.slide{
	    overflow:auto;
	    background-color: #fff;
	    padding-bottom: .34133333rem;
	    background-color: #f2f4f6;
	    display: flex;
	    .slide-item{
	    	display: inline-block;
	    	background:#fff;
		    width: 6.61333333rem;
		    padding-bottom: .64rem;
		    margin-right: .34133333rem;
		    box-shadow: 0 2px 10px 0 rgba(255,73,101,.14);
		    border-radius: 9px;
		    .img-container{
		    	position: relative;
			    width: 6.61333333rem;
			    height: 6.61333333rem;
			    overflow: hidden;
			    border-radius: .192rem .192rem 0 0;
			    background:#fff;
			    img.pos{
			    	position: absolute;
				    left: 0;
				    top: 0;
				    display: block;
				    width: 1.70666667rem;
				    height: 1.70666667rem;
				    border: 0;
			    }
		    }
		    .introduce{
		    	overflow: hidden;
			    white-space: nowrap;
			    word-break: break-all;
			    text-overflow: ellipsis;
			    margin: .42666667rem .128rem 0 .34133333rem;
			    font-size: 24px;
			    color: #3d3d3d;
		    }
		    .price{
			    display: flex;
			    justify-content: space-between;
			    align-items: flex-end;
			    width: 100%;
			    margin-top: .42666667rem;
			    span.lit{
			    	color: #ff4965;
	    			font-weight: 700;
	    			font-size:0.768rem;
			    }
			    span.buying{
			    	margin-right: .34133333rem;
				    text-align: right;
				    font-size: .512rem;
				    color: #8f8f8f;
			    }
		    }
	    }
	}
	
	.list-container{
	    padding-bottom: 2.77333333rem;
		min-height: 25rem;
		background-color: #f2f4f6;
    	overflow: hidden;
    	.each{
    		float: left;
		    width: 7.744rem;
		    margin-left: .17066667rem;
		    margin-top: .17066667rem;
		    background-color: #fff;
		    box-sizing: border-box;
		    border-radius: .192rem;
		    overflow: hidden;
		    position: relative;
		    p.pro-name{
		    	padding: .34133333rem .34133333rem 0;
			    margin-bottom: .34133333rem;
			    font-size: 26px;
			    color: #3d3d3d;
			    overflow: hidden;
			    white-space: nowrap;
			    word-break: break-all;
			    text-overflow: ellipsis;
		    }
		    p.oldprice{
		    	margin: 0 .34133333rem .34133333rem;
			    color: #8f8f8f;
			    font-size: 26px;
			    text-decoration: line-through;
		    }
		    p.nowprice{
			    padding: 0 .384rem .512rem;
			    color: #ff4965;
			    display: flex;
			    justify-content: space-between;
			    .in{
			    	line-height: .85333333rem;
    				color: #8f8f8f;
			    }
		    }
    	}
	}
	
	
</style>


