<template>
	<div id="box">
		<div id="search-bar-container">
			<a class="search-bar">
				<div class="search-inner">
					<span class="img search-img"></span>
					<span class="img flag-img"></span>
					<span class="title">搜索热销商品</span>
				</div>
			</a>
		</div>

		<div class="nav-menu-container">
			<div class="swiper-container nav">
				<div class="swiper-wrapper item-container">
					<a class="swiper-slide item" v-for='(item,num) in navArr' :class="num==index?'active':''" @click="change(num,item[1])"  :href="index==0?'#/group/son1':(index==10?'#/group/son3':'#/group/son2/'+item[1])" >{{item[0]}}</a>
				</div>
			</div>
		</div>
		
		<router-view></router-view>
		
		<footer class="foot">
			<a href="#/group"><div :class="footNum==1?'actives':''" @click="changeFootNum(1)"><span class="iconfont icon">&#xe615;</span><span>今日热团</span></div></a>
			<a href="#/hotSale"><div :class="footNum==2?'actives':''" @click="changeFootNum(2)"><span class="iconfont icon">&#xe60b;</span><span>热销榜</span></div></a>
			
			<a href="#/mine"><div :class="footNum==3?'actives':''" @click="changeFootNum(3)"><span class="iconfont icon">&#xe60d;</span><span>我的拼团</span></div></a>
			
		</footer>
		<go-top></go-top>
	</div>
</template>

<script>
	import GoTop from './GoTop'
	export default {
		name:'son1',
		data(){
			return {
				index: sessionStorage.num || 0,
				footNum:1,
				navArr: [
					['上新',''],
					['孕婴','momthings'],
					['童装','children_wear'],
					['女装','dress'],
					['玩具','toy'],
					['美妆','beauty'],
					['居家','house'],
					['食品','fooddrink'],
					['鞋包','shoes_bag'],
					['进口','health'],
					['下期预告','tomorrow']
				],
				defaultKey:'momthings'
			}
		},
		methods:{
			change(index,word) {
				this.index = index
				sessionStorage.num=index
				sessionStorage.keys = word || ''
			},
			
			changeFootNum(num){
				 this.footNum = num
			}
			
		},
		mounted(){
			var swiper = new Swiper('.nav', {
				slidesPerView: 'auto',
				spaceBetween: 5,
				freeMode: true
			});
		},
		components:{
			GoTop
		}
	}
</script>

<style scoped lang="scss">
	.actives{
		color:#ff4965;
	}
	.swiper-container{background:#fff}
	.search-bar {
		display: block;
		background: #fff;
		padding: .34133333rem .512rem;
		.search-inner {
			border: .02133333rem solid rgba(42, 50, 60, .2);
			border-radius: .17066667rem;
			padding: 0 .256rem;
			.img {
				position: relative;
				top: .10666667rem;
				background-size: 100%;
				display: inline-block;
			}
			.img.search-img {
				background: url(//h0.hucdn.com/open/201709/7b7654f8eb2512df_32x32.png);
				background-size: contain;
				width: .68266667rem;
				height: .68266667rem;
			}
			.img.flag-img {
				background: (url(//h0.hucdn.com/open/201709/b2ffbe6a078ad893_62x30.png));
				background-size: contain;
				margin-left: .10666667rem;
				width: 1.32266667rem;
				height: .64rem;
			}
			.title {
				color: #8f8f8f;
				line-height: 1.28rem;
				padding-left: .256rem;
				font-size: 0.6rem;
			}
		}
	}
	
	.item-container {
		padding-left: .21333333rem;
		padding-right: 0;
		background: #fff;
		.item {
			padding: 0 .42666667rem;
			color: #333;
			height: 1.728rem;
			line-height: 1.728rem;
			font-size: 0.6rem;
			width: auto;
		
		}
		.item.active {
			height: 1.664rem;
			border-bottom: .064rem solid #ff4965;
			color: #ff4965;
		}
	}
	.foot{
	    position: fixed;
	    padding:0.2rem;
	    bottom: 0;
	    width: 100%;
	    border-top: 1px solid #ccc;
	    background-color: #fff;
	    z-index: 99;
	    display: flex;
	    justify-content: space-around;
	    span{
	    	font-size:24px;
	    }
	    div{
	    	display: flex;
	    	flex-flow: column;
	    	align-items: center;
	    	.icon{
	    		font-size:1rem;
	    	}
	    }
	}
</style>


