<template>	
	<div class="main-box">	
		<div class="nav-box">
			<div class="waimian">
				<span class="nav-item" 
				v-for="(item,i) of navText" 
				@click="getnum(item[0],item[1],i)"
				:class="{active:index==i}"
				>{{item[2]}}</span>
			</div>
		</div>
   		<ul class="list-group"
		  v-infinite-scroll="loadMore"
		  infinite-scroll-disabled="loading"
		  infinite-scroll-distance="10">
		  <li class="xinpin-banner" v-for="index in arr" @click="goDetail(index.iid)">
		  	<img :src="index.img"/>
			<div class="xinpin-buttom">
				<div class="xinpin-one">
					<span class="xinpin-left">￥<i>{{index.price/100}}</i></span><span class="xinpin-right xinpin-zhe">{{index.discount_desc}}</span>
				</div>				
				<div class="xinpin-title">{{index.title}}</div>
					<div class="xinpin-shi">
					<span class="xinpin-left">{{new Date(index.time).getHours()%8}}小时前</span> <span class="xinpin-right">{{index.buying_info}}</span>
				</div>
			</div>	
		  </li>  
		</ul>
   		<go-top></go-top>
		<footnav></footnav>		
	</div>
</template>

<script>
import jsonp from 'jsonp'
import axios from 'axios'
import Footnav from './Footnav'
import GoTop from './GoTop'
import {mapActions} from 'vuex'

export default {
name:'new',
data(){
	return {
		arr:[],			
		msg:'',
		index:0,//控制导航栏变色
		num:0,
		navText:[
			['','newly','精选'],
			['activity-','channel','买一送一'],
			['9kuai9-','channel','9.9元'],
			['nvzhuang-','channel','女装'],
			['tongzhuang-','channel','童装'],
			['tongxie-','channel','童鞋'],
			['9wanju-','channel','玩具'],
			['xiebao-','channel','鞋包'],
			['yongpin-','channel','用品'],
			['jujia-','channel','居家'],
			['meizhuang-','channel','美妆']
		],
		msg2:"newly",
		loading:false
		// 精选
	}
},
methods:{
	getnum(res,res2,i){	
		this.msg = res;
		this.msg2 = res2;
		this.index=i;
		var that = this;		
		axios.get("discovery/"+this.msg2+"_recom_get/"+ this.num +"-"+ this.msg +Math.ceil(new Date().getTime()/1000)+".html")
		.then(function(res){
			that.arr=res.data.newly_recommend_products
		});
	},
	loadMore(){
		this.loading=true;
		var that = this;
		axios.get("discovery/"+this.msg2+"_recom_get/"+ ++this.num +"-"+ this.msg +Math.ceil(new Date().getTime()/1000)+".html")
		.then(function(res){	
			that.arr.push(...res.data.newly_recommend_products)
		});
	},
	...mapActions(['setGoodsId']),
	goDetail(iid){
		location.hash='detail'
		this.setGoodsId(iid)
	}
},
	mounted(){		
	},
	updated(){
		this.loading=false
	},
	components:{
		GoTop,
		Footnav
	}
}
</script>

<style scoped lang="scss">
 	.main-box{
		.list-group{
			overflow: hidden;
		}
	}
	
	.header{
		height: 1.92rem;
		background:-webkit-gradient(linear, 0% 0%, 100% 0%,from(#f8d6eb ), to(#c0c5fb));
		 overflow: hidden;text-align: center;
	}
	.header	div{
		float: left;width: 20%;height: 100%;
		font-size: 0.8rem;
		color: #fff;
	}
	a{
		line-height: .768rem;
	    font-weight: 700;
	    font-size: .64rem;color: #fff;
	}
	p{	line-height: .704rem;
   		 font-size: .512rem;
		
	}
	.main-box .nav-box{
		height: 1.8rem;
		overflow-x: scroll;
		background: #fff;
	}
	.nav-item{
		line-height:1.6rem;
		border-bottom: 0.2rem solid transparent;
		font-size: 28px;
		padding:0 0.5rem;
		display: inline-block;
		height: 100%;
		&.active{
			color: #ff4965;
    		border-bottom: .1rem solid #ff4965;
    	}
	}
	.swiper-slide-active{
		border-bottom-color: pink;
	}
	.xinpin-right{
		float: right;display: block;font-size: 0.55rem;
	}
	.xinpin-banner{
		height:11.5rem;
		width: 48.7%;
		float: left;
		margin: 0 0.1rem 0.2rem;
		background: #fff;
		border-radius: 0.3rem; 
	}
	.xinpin-one{
		width: 100%;
		height: 1.3rem;color: red;
	}
	.xinpin-one i{
		font-size:0.7rem ;
	}
	.xinpin-left{
		display: block;
		float: left;font-size: .55466667rem;
	}
	.xinpin-zhe{
		border: 1px solid deeppink;height: 0.7rem;font-size: 0.5rem;
		border-radius: 0.2rem;margin-top: 0.1rem;
	}
	
	.xinpin-buttom{
		padding: 0.2rem 0.3rem;
	}
	.xinpin-buttom .xinpin-title{
		height: 1rem;width: 100%;display: block;overflow: hidden;
		white-space: nowrap;text-overflow: ellipsis;font-size: .55rem;
	}
	.xinpin-shi{
		color: #ACACB4;
	}
	.waimian{
		white-space:nowrap;
		background:#fff;
	}	
</style>


