<template>
	<div class="souitem">
		<search-top :loadMore='loadMore'></search-top>
		<div class="item-top">
			<span class="top-one top-num" @click="sort('click_num',0)" :class="{active:index==0}">↓ 人气</span>
			<span class="top-two top-num" @click="sort('price',1)" :class="{active:index==1}">↑ 价格</span>
			<span class="top-three top-num"><i class="iconfont">&#xe605;</i> 筛选</span>
		</div>
		<section class="search-box">
			<ul class="list-group"
			  v-infinite-scroll="loadMore"
			  infinite-scroll-disabled="loading"
			  infinite-scroll-distance="10">
				<li class="item-box" 
					v-for="item in arr" 
					@click="goDetail(item.iid)">		  	
				  	<span class="over-tips">全球购</span>                    
					<img class="item-img" :src="item.img"/>    
					<div class="item-title">             
						{{item.title}}          
					</div> 
				      
					<div class="price">                         
						<span class="price-span">¥ <i>{{item.price/100}}</i></span>                      
						<span class="price-hua">
							¥<span>{{item.price_ori/100}}</span>
						</span>                     
						<span class="price-zhe">{{item.sale_tip}}</span>                 
					</div>            		  				  	
				</li>				  
			</ul>
			
		</section>
	
		<go-top home="home"></go-top>
	</div>
		
</template>

<script>
	import axios from 'axios'
	import jsonp from 'jsonp'
	import SearchTop from './SearchTop'
	import GoTop from './GoTop'
	import {mapActions} from 'vuex'
	
	
	export default {
		name:"searchlist",
		orderby:"",
		data(){
			return {
				index:0,
				arr:[],
				loading:false,
				num:0,
				url:''
			}
		},
		methods:{
			// 人气，价格排序
			sort(orderby,i){
				this.index = i;// 换点击的颜色				
				if(this.$options.orderby != orderby){
					this.$options.orderby = orderby
					this.arr.sort((item1,item2)=>{
						return item1[orderby]-item2[orderby]
					})
				}else{
					this.arr.reverse()
				}
			},
			loadMore(search){	
				var that = this	
				if(!~this.$route.params.searchId.indexOf('keyword')){
					this.url="http://sapi.beibei.com/item/search/"+ ++this.num +"-40-hot-"+this.$route.params.searchId+"-0--.html";
					jsonp(this.url,{
						name:'BeibeiItemSearch',
						prefix:'jsonp'
					},function(err,res){
						if(res){
							that.arr.push(...res.search_items);
						}
					})
				}else{
					this.url='/mroute.html?method=beibei.item.search&keyword='+encodeURI(location.href.split('keyword=')[1])+'&page=1&sort=hot&brand_ids=&%20cat_ids=';
					axios.get(this.url)
					.then(function(res){
						if(res&&search){
							that.arr=res.data.search_items
						}else if(res){
							that.arr.push(...res.data.search_items)
						}
					});
				}
							
				
			},
			...mapActions(['setGoodsId']),
			goDetail(iid){
				location.hash='detail'
				this.setGoodsId(iid)
			}
		},
		mounted(){

		},
		components:{
			SearchTop,
			GoTop		
		}	
	}
</script>

<style scoped lang="scss">
	.souitem{
		overflow: hidden;width: 100%;
		height: auto;
		.list-group{
			overflow: hidden;
		}
	}
	.souitem .search{
		height: 1.5rem;background: #fff;
	}
	.item-top{
		height: 2rem;width: 100%;display: flex;text-align: center;
		margin-top: -0.5rem;background: #fff;
	}
	.item-top i{
		font-size: .7rem;
	}
	.item-top .active{
		color: #FF4A7A;
	}

	.top-num{
		width: 33.3333%;
	    height: 1.152rem;
	   line-height: 2rem;
	    font-size: .6rem;
	    
	}
	.item-box{
		    float: left;background:#fff;
		    width:49.6%;
		    height: 11.81866667rem;
		    border-top: .05rem solid #e4e4e4;
		    border-right: .05rem solid #e4e4e4;
			position: relative;
	}
	.item-img{		
	    width:96%;
	    height: 7.68rem;
	    margin: .14933333rem;
	}
	.over-tips{
		    z-index:22;
		    position: absolute;
		    right:0.2rem;
		    top: 0.2rem;
		    color: #fff;
		    padding: .2rem;
		    display: inline;
		    font-size: .4rem;
		    background-color: #984aff;
		    width: 1.6rem;
		    text-align: center;
		    display: block;
	}
	.item-title{
		    padding-top: .25rem;
		    font-size: .6rem;
		    line-height: .79rem;
		    display: -webkit-box;
		    overflow: hidden;
		    word-break: break-all;
		    -webkit-box-orient: vertical;
		    -webkit-line-clamp: 2;
		    color: #666;
        	height: 1.6rem;
        	padding: 0.3rem .4rem 0 ;
	}
	.price{
		height: .68266667rem;
		padding: .7rem 0.25rem 0.4rem;
	}
	.price-span{
	    color: #ff4965;
	    font-size: .68266667rem;
	}
	.price-hua{
		margin-right: .1rem;
	    padding-top: .128rem;
	    margin-left: .2rem;
	    text-decoration: line-through;
	    color: #e4e4e4;
	    font-size: .55466667rem;
	}
	.price-zhe{
		padding-top: .128rem;
	    float: right;
	    color: #999;
	    font-size: .55466667rem;
	}
	

</style>


