<template>
<div class="zc-div">
	<form v-on:submit.prevent="login" action="">
	<div class="zhanghao dl-div">
		<i class="iconfont">&#xe687;</i>
		<input type="text" placeholder="请输入手机号码/邮箱" v-model="username"/>
	</div>
	
	<div class="mima  dl-div">
		<i class="iconfont">&#xe606;</i>
		<input type="password" placeholder="请输入6-16位密码" v-model="password"/>
	</div>
	
	<div class="dl-div">
		<input class="yanzheng" placeholder="请输入4位验证码" />
		<img  class="checkcode-img" title="看不清？换一张" :src="'//d.beibei.com/checkcode/show.html?t='+timeStamp" @click="changeSrc">
	</div>
	<div class="dl-sub">
		<input type="submit" value="立即登录" class="dl-submit" />		
	</div>	
	</form>
	<div class="dl-buttom">
		<span class="dl-a-left jump">新人注册</span>
		<span class="dl-a-center">手机号快速登录</span>
		<span class="dl-a-right">忘记密码</span>
	</div>
	<div class="dl-tishi" v-show="dlisShow">用户名或密码输入错误</div>
</div>


</template>

<script>
export default {
	name:"login",
	data(){
		return {
			dlisShow:false,
			username:"",
			password:"",
			timeStamp:+new Date()
		}
	},
	methods:{
		login:function(){
			var str = /^\d{11}$/
			if(str.test(this.username)){
				this.dlisShow = false
			}else{
				this.dlisShow = true;
			}
			var str2 = /^\w{6,16}$/
			if(str2.test(this.password)){
				this.dlisShow = false
			}else{
				this.dlisShow = true;
			}
		},
		changeSrc(){
			this.timeStamp=+new Date()
		}
	}
}
</script>

<style scoped lang='scss'>
	.zc-div{
		width: 100%;overflow: hidden;
	}
	
	.zhanghao{
		margin-top: 1rem;
	}
	.dl-div {
		position: relative;
		background:#fff;
	}
	.dl-div i{
		display: block;z-index: 22;
		position: absolute;font-size: 1rem;top: 0.7rem;left:.5rem;
	}
	.dl-div input{
		display: block;
	    z-index: 1;
	    position: relative;
	    width: 100%;
	    height: 1.88rem;
	    padding: .3rem  0rem  .3rem  2rem;	   
	    background-size: auto .93866667rem;
	    line-height: .72533333rem;
	    font-size: .64rem;
	    border-bottom: 1px solid #ccc;
	}
	.dl-div img{
		position: absolute;right: 0.3rem; height: 1.5rem;top: .5rem;
		width: 5rem;display: block;z-index: 1;
	}
	.dl-sub{text-align:center;}
	.dl-submit{
		width: 14.976rem;
	    height: 1.87733333rem;
	    border-radius: .17066667rem;
	    margin-top: .78933333rem;
	    line-height: 1.87733333rem;
	    font-size: .72533333rem;
	    text-align: center;
        background-color: #FF4965;
    	color: #FFF;
	}
	.dl-buttom{
		margin: .512rem;overflow: hidden;
		display:flex;justify-content:space-around;
		span{
			color: #3d3d3d;
			text-align:center;
		    font-size: .64rem;
		    display: block;
		    width: 4.992rem;
		}
	}

	.dl-tishi{
		width: 45%;
		height: 1.4rem ;
		background: #909091;margin:2rem auto;
		text-align: center;
		border-radius: 0.3rem;
		font-size: .6rem;line-height: 1.4rem;color: #fff;
	}
</style>


