<template>
	<section class="search">
		<div class="search-input" style="width: 13.976rem;">
			<!-- 当聚焦就变成12.46rem 13.976rem--> 
			<span class="iconfont search-span">&#xe647;</span> 
			<input v-model.lazy="msg" type="text" class="s-sousuo" placeholder="发现你喜欢的商品">
		</div>
		<span class="search-button" @click="toSearch">搜索</span>
	</section>
	
</template>

<script>
import jsonp from 'jsonp';
export default {
	name:"searchtop",
	props:['loadMore'],
	data(){
		return{
			msg:"",
			arr:[],
			arr2:[]
		}			
	},
	mounted(){
		// 执行聚焦出现搜索事件
		var searchinput = document.getElementsByClassName("search-input")[0];
		var searchbutton =  document.getElementsByClassName("search-button")[0];		
		document.getElementsByClassName("s-sousuo")[0].onfocus = function(){
			searchinput.style.width = "12.46rem";
			searchbutton.style.color = "#ff4965";			
		}			
		document.getElementsByClassName("s-sousuo")[0].onblur = function(){
			searchbutton.style.color = "#FFF"
			searchinput.style.width = "13.976rem"
		}
	},
	methods:{
		toSearch(){
			if(this.msg){
				location.hash='searchlist/keyword='+this.msg;
				this.loadMore&&this.loadMore('search');
				this.msg='';
			}
		}
	}
}
		
</script>

<style scoped lang="scss">
	.search{
	    height: 2rem;
	    padding: .512rem;
	    background-color: #fafafa;
	    width: 100%;
	    overflow: hidden;
	}
	.search .search-input{
		padding: .384rem;
	    display: inline-block;
	    height: .64rem;
	    border: 1px solid #bebebe;
	    border-radius: .21333333rem;
	    background-color: #fff;
        width: 90%;
        position: relative;
	}
	.search-span{
		position: absolute;
		display: block;
		top: 0.35rem;left: 0.45rem;
		font-size:.7rem ;
	}
	.s-sousuo{
		font-size: .64rem;
	    margin-left: .42666667rem;
	    line-height: .64rem;
		position: absolute;
		left: 1.4rem;top: 0.3rem;	
	}
	.search-button{
		
		position: absolute;
	    font-size: .64rem;
	    padding: .38rem;
	    display: inline-block;
	    color: #fff;
	    right: 0.1rem;
	    top: 0.35rem;
	}
	.iconfont {
		  font-family:"iconfont" !important;
		  /*font-size:16px;*/
		  font-style:normal;
		  -webkit-font-smoothing: antialiased;
		  -moz-osx-font-smoothing: grayscale;
		}
	
	
	
	@font-face {
	  font-family: 'iconfont';  /* project id 354819 */
	  src: url('//at.alicdn.com/t/font_eru607hu5mnp14i.eot');
	  src: url('//at.alicdn.com/t/font_eru607hu5mnp14i.eot?#iefix') format('embedded-opentype'),
	  url('//at.alicdn.com/t/font_eru607hu5mnp14i.woff') format('woff'),
	  url('//at.alicdn.com/t/font_eru607hu5mnp14i.ttf') format('truetype'),
	  url('//at.alicdn.com/t/font_eru607hu5mnp14i.svg#iconfont') format('svg');
	}
</style>


