<template>
	<div class="box">
			<div class="swiper-container  banner">
				<div class="swiper-wrapper">
					<div class="swiper-slide"><img src="//h0.hucdn.com/images/201726/08f81215aa7ff4b1_750x250.jpg!750.webp" /></div>
					<div class="swiper-slide"><img src="//h0.hucdn.com/images/201722/2033914d5ed997f2_750x250.jpg!750.webp" /></div>
					<div class="swiper-slide"><img src="//h0.hucdn.com/images/201722/56af0988d5a4d0cc_750x250.jpg!750.webp" /></div>
				</div>
				<div class="swiper-pagination"></div>
			</div>
	
			<div class="choose">
				<a v-for="info in about"><i class="info" :style="'background: url('+info.img+');background-size: contain'"></i>
					<p>{{info.title}}</p>
				</a>
			</div>
	
			<ul v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10" class="list-container">
				<li v-for="(item,index) in list" class="list-item" :key='index' :class="'s'+index" @click="goDetail(item.iid)">
					<div class='list-img' :style="'background:url('+item.rect_img+');background-size: contain'">
						<div class="left-top"><img :src="item.tags_v2?item.tags_v2[0]:''" /></div>
					</div>
					<div class="detail-container">
						<div class="msg">
							<span class="tit" v-for="msg in item.seller_labels">{{msg}}</span>
							<span>{{item.title}}</span>
						</div>
					</div>
				</li>
			</ul>
	</div>
</template>

<script>
	import {mapActions} from 'vuex'
	import jsonp from 'jsonp'
	import axios from 'axios'


	export default {
		name: 'son1',
		data() {
			return {
				about: [],
				list: [],
				order: 1
			}
		},
		methods: {
			loadMore() {
				this.getInfo(this.order)
				this.loading = true;
			},

			getInfo(num) {
				var that = this
				jsonp('http://sapi.beibei.com/fightgroup/home/' + num + '-40-0.html', {
					prefix: 'jsonp',
					name: 'BeibeiFightgroupHomeGet'
				}, function(err, res) {
					that.list = that.list.concat(res.fightgroup_items)
					that.loading = false
					that.order++
				})
			},
			...mapActions(['setGoodsId']),
			goDetail(iid){
				location.hash='detail'
				this.setGoodsId(iid)
			}
		},

		beforeMount() {
			this.getInfo(this.order)
		},

		mounted() {
			var that = this
			var swiper1 = new Swiper('.banner', {
				pagination: '.swiper-pagination',
				paginationClickable: true,
				autoplay: 4000,
				loop: true,
				autoplayDisableOnInteraction: false
			});

			axios.get('/ads/h5.html?ad_id=14').then(
				function(data) {
					that.about = data.data.h5_pintuan_icon_shortcuts
				});
			
			var top = document.body.scrollTop || document.documentElement.scrollTop
			var height = document.getElementById('search-bar-container').offsetHeight
			var fix = document.getElementsByClassName('nav-menu-container')[0]
			document.addEventListener('scroll',function(){
				if(top >= height) {
					fix.style.position = 'fixed'
					fix.style.left = 0;
					fix.style.top = 0;
					fix.style.zIndex = 99
				} else {
					fix.style.position = 'static'
				}
			})
		}
	}
</script>

<style scoped lang='scss'>

	.choose{background:#fff}
	.choose {
		a {
			width: 20%;
			display: inline-block;
			text-align: center;
			padding-bottom: 0.32rem;
			margin-bottom: 0.32rem;
			.info {
				display: block;
				margin: .512rem auto .128rem;
				background-repeat: no-repeat;
				background-size: 100% 100%;
				width: 2.56rem;
				height: 1.70666667rem;
			}
		}
		p{font-size:22px;}
	}
	
	.list-container {
		padding-bottom: 2.77333333rem;
		min-height: 25rem;
		.list-item {
			list-style: none;
			background-color: #fff;
			margin-bottom: .34133333rem;
			.list-img {
				position: relative;
				width: 100%;
				height: 7.46666667rem;
			}
			.left-top {
				position: absolute;
				width: 4.26666667rem;
				height: 4.26666667rem;
				z-index: 9;
				img {
					width: 100%;
				}
			}
			.detail-container {
				position: relative;
				margin-top: .512rem;
				padding: 0 .512rem;
				font-size: 0.55rem;
				.msg {
					overflow: hidden;
					white-space: nowrap;
					word-break: break-all;
					text-overflow: ellipsis;
					color: #333;
					padding-bottom:8px;
					span.tit {
						display: inline-block;
						padding: 0 .34133333rem;
						color: #3d3d3d;
						line-height: .81066667rem;
						height: .81066667rem;
						text-align: center;
						border-radius: .81066667rem;
						background: #eeeff0;
					}
				}
			}
		}
	}
</style>


