<template>
	<div class="bigbox">
		<section class="rush">
			<div class="tops">
				
			</div>
		</section>
		
		<ul v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
			<li class='production-list' v-for="x in prolist" @click="goDetail(x.iid)">
				<div class="img-box">
					<img :src="x.img"/>
				</div>
				<div class="cate-info">
					<p class="introduce">
						{{x.title}}
					</p>
					<p class="cate-text">
						<span class="origin-price">￥{{x.price/100}}</span>
						<span class="people">{{x.buying_info}}</span>
					</p>
					<p class="discount">
						<span>￥{{x.group_price/100}}</span>
					</p>
				</div>
			</li>
		</ul>
		<go-top home="home"></go-top>
	</div>
</template>

<script>
import GoTop from './GoTop'
import {mapActions} from 'vuex'
import jsonp from 'jsonp';

export default {
	name:'hotSale',
	data(){
		return {
			prolist:[],
			order:1
		}
	},
	methods:{
		loadMore() {
			this.getInfo(this.order)
			this.loading = true;
		},
		getInfo(num){
			var that = this
			jsonp('http://sapi.beibei.com/fightgroup/hot/'+num+'-20-new_hot.html',{
				prefix:'jsonp',
				name:'BeibeiFightgroupHotGet'
			},function(err,res){
				if(res){
					that.prolist = that.prolist.concat(res.fightgroup_items)
					that.loading = false
					that.order++
				}
			})
		},
		...mapActions(['setGoodsId']),
		goDetail(iid){
			location.hash='detail'
			this.setGoodsId(iid)
		}
	},
	beforeMount(){
		this.getInfo(this.order)
	},
	components:{
		GoTop
	}
}
</script>

<style scoped lang="scss">
	.rush{
		height:3.2rem;
		background: url("//h0.hucdn.com/open/201718/34350f7ea35a8b34_750x150.png") no-repeat;
		background-size: 16rem 3.2rem;
		.tops{
			width: 4.26666667rem;
		    height: 3.2rem;
		    margin: 0 auto;
		    background: url(//h0.hucdn.com/open/201718/e1a5a836744f3719_200x150.png) no-repeat;
		    background-size: 4.26666667rem 3.2rem;
		}
	}
	
	.production-list{
		display: flex;
	    box-sizing: border-box;
	    height: 6.912rem;
	    padding: .256rem .256rem 0;
	    background:#fff;
	    .img-box{
	    	position: relative;
		    flex: 0 0 auto;
		    height: 6.4rem;
		    overflow: hidden;
		    img{
		    	display: inline-block;
			    border: 0;
			    width: 6.4rem;
			    height: 6.4rem;
		    }
	    }
	    .cate-info{
		    flex: 1 1 auto;
		    position: relative;
		    width: 9.344rem;
		    box-sizing: border-box;
		    padding: .42666667rem .256rem .42666667rem 0;
		    margin-left: .384rem;
		    border-bottom: 1px solid rgba(0,0,0,.08);
		    .introduce{
		    	color: #3d3d3d;
			    overflow: hidden;
			    word-break: break-all;
			    font-size:.6rem;
		    }
		    .cate-text{
	    	    position: absolute;
			    bottom: 1.96266667rem;
			    width: 8.448rem;
			    font-size:24px;
			    color: #8f8f8f;
			    display: flex;
			    justify-content: space-between;
			    .origin-price{
			    	text-decoration: line-through;
			    }
			    .people{
			    	color:#ff5752;
			    }
		    }
		    .discount{
		    	position: absolute;
			    left: 0;
			    bottom: .68266667rem;
			    span{
			    	color:#ff5752;
			    	font-size:30px;
			    	font-weight:600;
			    }
		    }
	    }
	}
	
</style>


