import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

import actions from './actions'


let state = {
	goodsId:sessionStorage.goodsId,
	goodsList:JSON.parse(sessionStorage.goodsList||'[]')
}


let getters = {
	doublenum:function(state){
		return state.num*2
	}
}

let mutations = {
	setGoodsId(state,id){
		state.goodsId=id;
		sessionStorage.goodsId=id;
	},
	addGoods(state,[good,num]){
		
		//要是传来的good可以是对象也可以是商品的 id-->goodsId
		//num为传入的数   -1 +1  -1是减少   +1是增加
		var index=state.goodsList.findIndex(item=>
			item.goodsId==(good.goodsId||good)
		)

		if(!~index){//相当于 index<0
			good.txt='编辑';
			state.goodsList.push(good)
		}else{
			if(num){//如果num为 -1 或者 +1	
				state.goodsList[index].num+=num;
				if(state.goodsList[index].num<1){
					state.goodsList[index].num=1;
				}
			}else if(num==0){//num为0就是要删除
				state.goodsList.splice(index,1)
			}else{//num不存在就是改选中状态
				state.goodsList[index].check=!state.goodsList[index].check;
				for(var i=0,len=state.goodsList.length;i<len;i++){
					if(!state.goodsList[i].check){
						state.goodsList.all=false;
						break;
					}else{
						state.goodsList.all=true;
					}
				}
			}
		}
		state.goodsList.money=state.goodsList.save=state.goodsList.num=0;
		state.goodsList.forEach((good,i,goods)=>{
			if(good.check){
				goods.money+=good.groupPrice*good.num;
				goods.num+=good.num
				goods.save+=(good.price-good.groupPrice)*good.num;
			}
		})
		sessionStorage.goodsList=JSON.stringify(state.goodsList);
	},
	allCheck(state,bool){
		state.goodsList.money=state.goodsList.save=state.goodsList.num=0;
		state.goodsList.forEach((good,i,goods)=>{
			good.check=bool;
			if(good.check){				
				goods.money+=good.groupPrice*good.num;
				goods.num+=good.num
				goods.save+=(good.price-good.groupPrice)*good.num;
			}
		})
		state.goodsList.all=bool;
		sessionStorage.goodsList=JSON.stringify(state.goodsList);
	}
}

let store = new  Vuex.Store({
	state,
	mutations,
	getters,
	actions
})

export default store


