<template>
	<div class="go-top">
		<div class="go-home" :class="{show,width}" v-show="home">
			<a href="#/main">
				<b class='iconfont'>&#xe608;</b>首页
			</a>
			<a href="#/car">
				<b class="iconfont">&#xe8db;</b>购物车
			</a>
			<a href="#/mine">
				<b class="iconfont">&#xe601;</b>我的
			</a>
			<span class="iconfont" @click="changeText" v-html='text'></span>
		</div>
		<transition name="top">
	    	<i class="iconfont btn" v-show="show" @click='goTop'>&#xe604;</i>
	  	</transition>
	</div>
</template>

<script>
export default {
  name: 'gotop',
  props:['home'],
  data () {
    return {
    	show:false,
    	width:false,
    	text:'&#xe77c;'
    }
  },
  methods:{
  	goTop(){
 		document.documentElement.scrollTop=window.pageYOffset=document.body.scrollTop=0;
  	},
  	changeText(){
  		this.width=!this.width;
  		this.text='&#xe77c;&#xe633;'.replace(this.text,'')
  	}
  },
  mounted(){
  	let that=this;
  	document.addEventListener('scroll',function(){
  		var top=document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
  		that.show=(top>1000)
  	})
  }
}
</script>

<style scoped lang="scss">
	.go-top{
		width:1.88rem;height:10rem;
		position: fixed;
		bottom: 0;
		right: 0.64rem;
		z-index:99;
		.go-home{
			display:block;
			position:absolute;
			bottom:2.75rem;right:0;
			width:1.88rem;
			/*width:7.52rem;*/
			border-radius:.94rem;		
			background:#333;
			height:1.88rem;
			transition:all .5s;
			overflow-y:hidden;
			&.width{
				width:7.52rem;
				padding-left:.5rem;
			}
			&.show{
				transform:translateY(-2.6rem);
			}
			>*{
				display:flex;
				width:1.88rem;
				height:100%;
				color:#fff;
				float:left;	
				justify-content:center;
				align-content:center;
			}
			a{
				flex-wrap:wrap;
				justify-content:center;
				font-size:20px;text-align:center;
				line-height:.8rem;	
				b{display:block;width:100%;font-size:30px;}
			}
			span{
				background:#333;
				border-radius:.94rem;
				position:absolute;
				right:0;top:0;
				font-size:36px;
				line-height:1.88rem;
			}
		}
	}
	.btn{
	display:block;    	
    width: 1.88rem;
    height: 1.88rem;
    position:absolute;
    bottom:0;right:0;
    border-radius:50%;
    border: 1px solid #b1b0b5;
    z-index: 99;
    background:#fff;
    line-height: 1.88rem;
    font-size: 0.93rem;
    text-align: center;
    cursor: pointer;
    transform: translateY(-2.75rem);
    opacity: 1;
    transition: all .5s;
	}
	.top-enter, .top-leave-to /* .top-leave-active in below version 2.1.8 */ {
	  transform: translateY(-0.23rem);
	  opacity:.2;
	}
</style>



