<template>
	<div class="zc-div">
		<form action="" v-on:submit.prevent="login">					
			<div class="zhanghao dl-div">
				<i class="iconfont">&#xe687;</i>
				<input type="text" placeholder="请输入手机号码" v-model="username"/>
			</div>			
			<div class="mima  dl-div ">
				<i class="iconfont">&#xe69b;</i>
				<input type="password" placeholder="请输入短信验证码" />
			</div>			
			<div class="dl-div">
				<i class="iconfont">&#xe606;</i>
				<input class="yanzheng" placeholder="请设置密码 ( 6-16个字符 ) " v-model="password"/>
				
			</div>			
			<div class="dl-sub">				
				<input type="submit" value="免费注册" class="dl-submit"/>
			</div>
			</form>		
			<div class="dl-buttom">
				<i class="jump">已有账号，请登录</i>
				<p>注册贝贝，即表示您同意</p>
				<p><a href="">《贝贝网使用协议》</a>和<a href="">《社区使用协议》</a></p>
			</div>				
			
			
			<div class="dl-tishi" v-show="dlisShow">用户名或密码输入错误</div>
	</div>

</template>
<script>
export default {
	name:"Register",
	data(){
		return {
			username:'',
			password:'',
			dlisShow:false
		}
	},
	methods:{
		login:function(){
			var str = /^\d{11}$/
			if(str.test(this.username)){				
				this.dlisShow = false
			}else{
				this.dlisShow = true;			
			}
			var str2 = /^\w{6,16}$/
			if(str2.test(this.password)){				
				this.dlisShow = false
			}else{
				this.dlisShow = true;
			}
		
		}
	}
}
</script>

<style scoped lang="scss">
	.zc-div{
		width: 100%;overflow: hidden;
	}
	.zhanghao{
		margin-top: 1rem;
	}
	.dl-div {
		position: relative;
		background:#fff;
	}
	.dl-div i{
		z-index: 22;display: block;
		position: absolute;font-size: 1rem;top: 0.7rem;left:.5rem;
	}
	
	.dl-div input{
		display: block;
	    z-index: 1;
	    position: relative;
	    width: 100%;
	    height: 1.88rem;
	    padding: .3rem  0rem  .3rem  2rem;	   
	    background-size: auto .93866667rem;
	    line-height: .72533333rem;
	    font-size: .64rem;
	    border-bottom: 1px solid #ccc;
	}
	.dl-sub{text-align:center;}
	.dl-submit{
		width: 14.976rem;
	    height: 1.87733333rem;
	    border-radius: .17066667rem;
	    margin-top: .78933333rem;
	    line-height: 1.87733333rem;
	    font-size: .72533333rem;
	    text-align: center;
        background-color: #FF4965;
    	color: #FFF;
	}
	.dl-buttom{
	    color: #8f8f8f;
	    line-height: .81066667rem;
	    text-align: center;
	    padding:.5rem 0 1rem;
	    font-size: .7rem;
	    .jump{
	    	background-color: #FF4965;
    		color: #FFF;border-radius:10px;
    		padding:16px;
    		display:inline-block;
    		line-height:1rem;
	    }
	    p{
	    	line-height:1rem;
	    }
	}
	.dl-buttom a{
		color: red;
	}
		.dl-tishi{
		width: 45%;
		height: 1.4rem ;
		background: #909091;margin:2rem auto;
		text-align: center;
		border-radius: 0.3rem;
		font-size: .6rem;line-height: 1.4rem;color: #fff;
	}	
</style>


