<template>
	<div class="prodetail">
		<div class="top-head">
			<div class="top-main">
				<div class="img-box">
					<img :src="headdata.brand_logo"/>
				</div>
				<div class="brand-text">{{headdata.brand_name}}</div>
				<div class="count-people">{{headdata.buying_info}}</div>
				<div class="store">
					<div class="iconfont">&#xe600;</div>
					<div class="">收藏</div>
				</div>
			</div>	
		</div>
		
		<section class="main-list">
			<header class="head">
				<span class="ico"></span>
				<span class="todaybuy">{{todaybuy.title}}</span>
			</header>
			<a v-for="x in todaybuy.hot_items" class="each-hot" @click="goDetail(x.iid)">
				<div class="img-box">
					<img :src="x.img">
					<div class="top-left"></div>
				</div>
				<div class="info-box">
					<div class="info-box-top">{{x.title}}</div>
					<div class="about-price">
						<p>
							<span class="dis">￥{{x.price/100}}</span>
							<span class='origin-price'>￥{{x.price_ori/100}}</span>
						</p>
						<span class="percent">{{x.sale_tip}}</span>
					</div>
				</div>
			</a>
		</section>
		
		<section class="hot-header">
			<header class='head'>
				<span class="ico"></span>
				<span class="todaybuy">正在热卖</span>
			</header>
			<ul class="nav">
				<li v-for="x in navArr" @click="getInfo(x[1])">{{x[0]}}</li>
			</ul>
		</section>
		
		<section class="todayhot">
			<a v-for="x in saling" class="each-hot" @click="goDetail(x.iid)">
				<div class="img-box">
					<img :src="x.img">
				</div>
				<div class="info-box">
					<div class="info-box-top">{{x.title}}</div>
					<div class="about-price">
						<p>
							<span class="dis">￥{{x.price/100}}</span>
							<span class='origin-price'>￥{{x.price_ori/100}}</span>
						</p>
						<span class="percent">{{x.sale_tip}}</span>
					</div>
				</div>
			</a>
		</section>
		
		<section class="guess">
			<header class='head'>
				<span class="ico"></span>
				<span class="todaybuy">猜你喜欢</span>
			</header>
			<div class="event" v-for="x in guess">
				<div class="brand" @click="getInfo('hot',x.event_id)">
					<img class="log" :src="x.logo" />
					<span class="info-wraper">
						<span class="brand-name">{{x.title}}</span>
						<span class="promation">{{x.buying_info}}</span>
					</span>
				</div>
				<div class="items">
					<div class="item" v-for="y in x.recom_items">
						<img :src="y.img"/>
						<p>{{y.title}}</p>
						<div>{{y.price/100}}</div>
					</div>
				</div>
			</div>
		</section>
		<go-top home="home"></go-top>
	</div>
</template>

<script>
	import GoTop from './GoTop'
	import jsonp from 'jsonp'
	import {mapActions} from 'vuex'
	export default {
		name:'mainclassify',
		data(){
			return {
				saling:[],
				guess:[],
				headdata:{},
				todaybuy:[],
				navArr:[
					['推荐','new'],
					['新品','hot'],
					['销量','sale_num'],
					['价格','price_asc'],
					['筛选','']
				]
			}
		},
		methods:{
			getInfo(keys,numbers){
				var num = this.$route.params.classifyId 
				var that = this
				jsonp('http://sapi.beibei.com/martshow/item/new/'+num+'-1-'+keys+'-0-0-0-0.html',{
					prefix:'jsonp',
					name:'BeibeiMartshowItemNewGet'
				},function(err,res){
					that.headdata = res.brand_info
					that.todaybuy = res.hot_items_info
					that.saling = res.martshow_items
					that.guess = res.recom_martshows_lists
					that.navArr[4][1]=keys
					if(keys == 'price_asc'){
						that.navArr[3][1] = 'price_desc'
					}
					if(keys == 'price_desc'){
						that.navArr[3][1] = 'price_asc'
					}
					document.body.scrollTop = 0
				})
			},
			...mapActions(['setGoodsId']),
			goDetail(iid){
				location.hash='detail'
				this.setGoodsId(iid)
			}
		},
		beforeMount(){
			this.getInfo(this.navArr[0][1])
		},
		components:{
			GoTop
		},
		updated(){			
			var tophead = document.getElementsByClassName('top-head')[0].offsetHeight
			var mainlist = document.getElementsByClassName('main-list')[0].offsetHeight
			var head = document.getElementsByClassName('head')[1].offsetHeight
			var dom = document.getElementsByClassName('nav')[0]
			document.addEventListener('scroll',function(){
				var top = document.body.scrollTop||document.documentElement.scrollTop
				if(top>=(tophead+mainlist+head)){
					dom.style.position='fixed'
					dom.style.top=0
					dom.style.left=0
				}else{
					dom.style.position=''
				}
			})
		}
	}
</script>

<style scoped lang="scss">
	
	.actives{
		color:#ff4965;
	}

	.top-head{
		padding: .512rem;
    	background: #fff;
    	.top-main{
    		position: relative;
		    padding-left: 3.5rem;
		    height: 3.15733333rem;
		    .img-box{
	    	    box-sizing: border-box;
			    border: 1px solid rgba(0,0,0,.08);
			    width: 3rem;
			    height: 3rem;
			    position: absolute;
			    left: .0;
			    bottom: 0;
			    background: #fff;
		    }
		    .brand-text{
	    	    padding-top: .6rem;
			    margin-bottom: .42666667rem;
			    font-size:0.8rem;
			    color: #3d3d3d;
		    }
		    .count-people{
		    	padding-bottom: .23466667rem;
			    font-size: 12px;
			    color: #8f8f8f;
			    font-size:0.6rem;
		    }
		    .store{
		    	position: absolute;
			    top: .768rem;
			    right: 0;font-size:30px;
			    text-align: center;
			    .star{
			    	font-size:30px;
			    }
		    }
    	}
	}
	
	header.head{
		height: 1.92rem;
	    width: 100%;
	    text-align: center;
	    line-height: 1.92rem;
	    color: #2a323d;
	    font-weight: 700;
	    background: #fff;
	    font-size:28px;
	    border-bottom: 1px solid rgba(0,0,0,.08);
	    .ico{
	    	display: inline-block;
		    width: .64rem;
		    height: .64rem;
		    vertical-align: middle;
		    margin-right: .17066667rem;
		    margin-top: -.10666667rem;
		    background: url(//h0.hucdn.com/open/201651/84583e9f60fe9c79_30x30.png) no-repeat 0 0/contain;
	    }
	    
    }
	
	.todayhot{
		overflow: hidden;
	}
	
	.main-list{
		overflow: hidden;
    	width: 100%;
    	.top-left{
			position: absolute;
		    top: 0;
		    left: 0;
		    background: url(//b0.hucdn.com/party/2017/3/upload_014ad3e368cee22026c304f46b43ffe2_80x80.png) no-repeat;
		    width: 1.70666667rem;
		    height: 1.70666667rem;
		    background-size: contain;
		}
	}
	
	.each-hot{
		float: left;
	    display: block;
	    width: 50%;
	    box-sizing: border-box;
	    color: #3d3d3d;
	    background: #fff;
	    font-size: 13px;
	    line-height: 1;
	    border-top: 1px solid rgba(0,0,0,.08);
	    border-left: 1px solid rgba(0,0,0,.08);
	    .img-box{
	    	position: relative;
			height: 8rem;
			img{
				display: block;
			    width: 100%;
			    height: 100%;
			}
			
	    }
	    .info-box{
	    	padding: .21333333rem .34133333rem .512rem;
	    	.info-box-top{
    		    line-height: 1.3;
    		    font-size:26px;
    		    overflow:hidden;
    		    white-space:nowrap;
    		    text-overflow: ellipsis;
			    margin-bottom: .384rem;
			    height: 36px;
			    overflow: hidden;
			    word-break: break-all;
				height:1.39rem;
	    	}
	    	.about-price{
	    		display: flex;
	    		justify-content: space-between;
	    		.dis{
					font-size:0.618rem;
					color:#ff4965;
				}
				.origin-price,.percent{					
					color:#8f8f8f;
					font-size:0.5rem;
					margin-left:0.06rem;
				}
				.origin-price{
					text-decoration: line-through;
				}
	    	}
	    }
	}
	
	.nav{
		position: relative;
	    z-index: 1;
	    background: #fff;
	    line-height: 0;
	    white-space: nowrap;
	    width: 100%;
	    overflow: hidden;
	    border-bottom: 1px solid rgba(0,0,0,.08);
	    li{
	    	width: 20%;
		    display: inline-block;
		    text-align: center;
		    position: relative;
		    line-height: .68266667rem;
		    padding: .512rem 0 .55466667rem;
		    font-size:0.6rem;
	    }
	}
	
	.guess{
		.event{
			position: relative;
		    color: #3d3d3d;
		    border-top: 1px solid rgba(0,0,0,.08);
		    padding: .512rem .512rem 0;
		    .brand{
   			 	.log{
   			 		width: 3.072rem;
				    height: 1.536rem;
				    vertical-align: middle;
   			 	}
   			 	.info-wraper{
   			 		padding-top: 0.17066667rem;
				    display: inline-block;
				    vertical-align: middle;
				    .brand-name{
				    	display: block;
				    	font-size:0.541rem;
				    }
				    .promation{
				    	color:#8f8f8f;
				    	display: block;
    					line-height: 1.024rem;
				    }
   			 	}
		    }
		    .items{
		    	padding-top: .256rem;
    			overflow: hidden;
    			.item{
    				float: left;
				    position: relative;
				    width: 4.82133333rem;
				    img{
				    	display: block;
						height: 4.82133333rem;
				    }
				    p{
				    	margin-top: .192rem;
					    line-height: 1.33333333;
					    height: 0.64rem;
					    overflow: hidden;
					    white-space: nowrap;
					    word-break: break-all;
					    text-overflow: ellipsis;
				    }
				    div{
				    	margin: .256rem 0 .512rem;
    					text-align: center;
    					color:#ff4965;
				    }
    			}
		    }
		}
	}
	
</style>


