<template>
  <div class="main-box">
		<component :is="subCom" @click.native="changeSub"></component>
  </div>
</template>

<script>
import Register from './Register'
import Login from './Login'
export default {
  name: 'mine',
  data () {
    return {
      subCom:'Login'
    }
  },
  methods:{
  	changeSub($event){
  		if($event.target.className.indexOf('jump')>-1){
  			this.subCom='RegisterLogin'.replace(this.subCom,'')
  		}
  	}
  },
  components:{
  	Register,
  	Login
  }
}
</script>

<style scoped lang="scss">
 .main-box{
 	height:100%;width:100%;
 	position:relative;

 }
</style>



