import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import MainClassify from '@/components/MainClassify'

import Group from '@/components/Group'
import son1 from '@/components/GroupSub/son1'
import son2 from '@/components/GroupSub/son2'
import son3 from '@/components/GroupSub/son3'

import New from '@/components/New'
import Car from '@/components/Car'
import Mine from '@/components/Mine'
import Search from '@/components/Search'
import SearchList from '../components/SearchList'
import Detail from '@/components/Detail'
import HotSale from '../components/HotSale'

Vue.use(Router)

export default new Router({
  routes: [
		{path: '/',redirect:"/main/0"},
    {path: '*',redirect:"/main/0"},
    {path: '/main/:mainid',name: 'main',component: Main},
    {path: '/classify/:classifyId',name: 'mainclassify',component: MainClassify},
    {
    	path: '/group',
    	name: 'group',
    	component: Group,
			children: [
				{
					path: 'son1',
					name: 'son1',
					component: son1
				},
				{
					path: 'son2/:key',
					name: 'son2',
					component: son2
				},
				{
					path: 'son3',
					name: 'son3',
					component: son3
				},
			]
		},
    {path: '/new',name: 'new', component: New},
    {path: '/car',name: 'car',component: Car},
    {path: '/mine',name: 'mine',component: Mine},
    {path: '/search',name: 'search',component: Search},
     {path: '/searchlist/:searchId',name: 'searchlist',component: SearchList},
    {path: '/detail',name: 'detail',component: Detail},
    {path:'/hotSale',name:'/hotSale',component:HotSale}
  ]
})

