<template>
  <div class="main-box">
		<!--<div class="car-box">
			<img src="/static/carlogo.png"/>
			<p>您还没登录,请先登录哦~</p>
			<router-link to="mine">登录贝贝</router-link>
		</div>-->
		<div class="goods-list">
			<div class="goods" v-for="good of goodsList">
				<h2 class="tit">
					<p>{{good.tit}}</p>
					<span
						 @click='controller(good)'
						 >{{good.txt}}</span>
				</h2>
				<div class="good-box">
					<i class="check-box iconfont" 
						:class="{checked:good.check}"
						@click="changeGoods(good.goodsId)">&#xe643;</i>
					<img :src="good.src"/>
					<div class="info-box">
						<h3>{{good.tit}}</h3>
						<p><span>¥{{good.groupPrice}}</span><del>¥{{good.price}}</del><i>x{{good.num}}</i></p>
						<!--控制加减的布局-->
						<div class="btn-box" :class={show:good.show}>
							<button @click='changeGoods(good.goodsId,-1)'>-</button>
							<i class="num">{{good.num}}</i>
							<button @click='changeGoods(good.goodsId,1)'>+</button>
							<span @click='changeGoods(good.goodsId,0)'>删除</span>
						</div>
					</div>
				</div>
				<h2 class="price">小计 : ¥
					{{(!good.check&&'0.00')||currency(good.groupPrice*good.num)}}
					<!--要是不选中给个0.00,选中就计算-->
				</h2>
			</div>
		</div>
		<div class="result">
			<i class="check-box iconfont" @click='allChecked' :class="{checked:goodsList.all}">&#xe643;</i>
			<ul class="info-box">
				<li @click='allChecked'>全选</li>
				<li class="money">
					<p>总计 : ¥{{currency(goodsList.money)}}</p>
					<p>为您节省 : ¥{{currency(goodsList.save)}}</p>
				</li>
				<li>结算({{goodsList.num}})</li>
			</ul>
		</div>
  	<Footnav></Footnav>
  </div>
</template>

<script>
import Footnav from './Footnav'
import {mapActions,mapState} from 'vuex'
export default {
  name: 'car',
  data () {
    return {
    	txt:false,
    	check:false
    }
  },
  computed:{
  	...mapState(['goodsList'])
  },
  methods:{
  	...mapActions(['addGoods','allCheck']),
  	controller(good){
  		good.show=!good.show
  		if(!good.txt){good.txt='编辑'}
  		good.txt='编辑完成'.replace(good.txt,'');
  	},
  	changeGoods(id,num){
  		this.addGoods([id,num])
  	},
  	allChecked(bool){ 
  		bool=!this.goodsList.all
  		this.allCheck(bool)
  	},
  	currency(str){
			if(!str){return '0.00'}
//		str+='';//确保str是字符串(以防止传入的为数字)  		
//		//小数部分并且是  .00的格式
//		float='.'+((str.split('.')[1]||'0')+'0000').slice(0,2)
//		str=str.split('.')[0]//取出整数部分
//
//			str=str.split('').reverse().join('');//字符串先转数组再倒序再转字符串;
//			str=str.replace(/\d{3}\B/g,$0=>$0+',');//进行正则替换;
//			//替换后的结果先转数组再倒序再转字符串;
//			return str.split('').reverse().join('')+float;	
				str*=1//确保str为数字
				return str.toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, '$1,')
		}
  },
  components:{
  	Footnav
  }  
}
</script>

<style scoped lang="scss">
 .main-box{
 	width:100%;
 	position:relative;
	margin-bottom:78px;
	overflow:hidden;
 }
 .car-box{
	 	padding-top:2rem;
	 	text-align:center;
	 	font-size:.65rem;
	 	line-height:2.5em;
		img{width:40%;}
		a{
			border:1px solid #f7afbc;
			display:inline-block;background:#fff;
			line-height:2em;padding:0 18px;
			color:#ff4965;border-radius:5px;
		}
 }
 .check-box{
 		display:block;
 		width:1rem;
 		line-height:1rem;
 		margin:.5rem;
 		font-size:30px;
 		border:1px solid #999;
 		border-radius:50%;
 		text-align:center;
 		color:#fff;
 	}
 	.checked{
 		background:deeppink;
 	}
 	.result{
 		position:fixed;bottom:2.3rem;left:0;width:100vw;
 		background:#fff;border-top:1px solid #e4e4e4;height:2rem;
 		display:flex;
 		.info-box{
 			flex:1;display:flex;
 			ul{
 				display:flex;
 			}
 		}	
 		li{
 			font-size:30px;line-height:2rem;
 			width:15%;
 			
 			&:last-child{
 				width:35%;
 				text-align:center;
 				background:hotpink;
 				font-weight: 600;
 				color:#fff;
 			}
 			&.money{
 				width:50%;
 				padding-right:10px;
 				text-align:right;
 				p{line-height:1rem;font-size:.6rem;
					&:first-child{color:deeppink;} 				
 				}
 			}
 		}
 	}
 .goods{
	 	background:#fff;
	 	margin-bottom:.3rem;
	 	
	 	del{padding-left:10px;}
	 	
	 	.good-box{
	 		display:flex;
	 		padding:.5rem 0;
	 		height:4.8rem;
	 		align-items:center;
	 		img{
	 			width:3.8rem;
	 			height:3.8rem;
	 		}
	 		.info-box{
	 			display:flex;
	 			position:relative;
	 			flex-direction:column;
	 			justify-content:space-between;
	 			flex:1;height:3.8rem;
	 			padding:0 .8rem;
	 			h3{font-size:.6rem;overflow:hidden;height:90px;}
	 			.btn-box{
	 				padding:0px 1rem;
	 				position:absolute;left:0;top:0;background:#fff;
	 				width:100%;height:100%;line-height:3.8rem;
	 				visibility:hidden;font-size:30px;
	 				button{
	 					width:1.4rem;height:1.4rem;background:#ccc;font-size:40px;
						vertical-align:middle;
	 				}
	 				.num{
	 					display:inline-block;background:#ccc;
	 					float:none;vertical-align:middle;
	 					height:1.4rem;width:2rem;text-align:center;line-height:1.4rem;
	 				}
	 				span{color:#fff;background:deeppink;font-size:30px;padding:0 15px;
	 					vertical-align:middle;display:inline-block;
	 				}
	 				&.show{visibility:visible;}
	 			}
	 			p{
	 				font-size:30px;color:#999; 				
	 			}
	 			span{
	 				color:#333;font-weight:600;
	 			}
	 			i{float:right}
	 		}
	 	}
	 	h2{
	 		margin-left:2rem;
	 		font-size:30px;
	 		&.tit{
	 			border-bottom:1px solid #999;
	 			p{
	 				width:70%;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;
	 				display:inline-block;line-height:1.5rem;
	 			}
	 			span{padding:0px 30px;line-height:1.5rem;float:right;}
	 		}
	 		&.price{
	 			border-top:1px solid #999;
	 			text-align:right;padding-right:1rem;
	 			color:#666;line-height:1.5rem;
	 		}
	 	}
 }
</style>



