<template>
  <div class="main-box">
  	<div class="top-nav space-box swiper-container">
  		<div class="top-nav-box swiper-wrapper">
  				<router-link 
  					class="swiper-slide"
  					v-for="(item,index) of navText" 
  					:to="'/main/'+index" 
  					:key="index" 
  					@click.native="changePage(item[1])"
  					:class=''
  				>{{item[0]}}</router-link>				
  		</div>
  		<div class="search">
  			 <span class="son"></span>
  			 <router-link class="iconfont son" to="/search">&#xe609;</router-link>
  		</div>
  	</div>
  	<div class="item-box space-box" v-show="imgItem.length"
  		>
  		<div v-for="(item,index) in imgItem" :class="{'border-none':index>2}">
	  		<img :src="item.img"/>
  		</div>
  	</div>
  	<div v-html="headline" class="headline"></div>
  	<ul
		  v-infinite-scroll="loadMore"
		  infinite-scroll-disabled="loading"
		  infinite-scroll-distance="10"
		>
		  <li v-for="item in list" class="good-box space-box" @click='toClassify(item[item.type].event_id)'>
		  	<div class="tit-box">
		  		<img :src="item[item.type].brand_logo||'/static/logo.jpg'"/><span>{{item[item.type].brand_name}}</span><span>{{item[item.type].title}}</span><i class="iconfont">&#xe60a;</i>
		  	</div>
		  	<div class="goods-wrap">
		  		<div class="goods-info" v-for="i of item[item.type].items">
		  			<img :src="i.img"/>
		  			<h2>¥<span>{{i.price/100}}</span><i>¥{{i.price_ori/100}}</i></h2>
		  			<h3>{{i.title}}</h3>
		  		</div>
		  	</div>
		  </li>
		</ul>
  	<footnav></footnav>
  	<go-top home=""></go-top>
  </div>
</template>

<script>
import jsonp from 'jsonp'
import Footnav from './Footnav'
import GoTop from './GoTop'

export default {
  name: 'main',
  data () {
    return {
      navText:[
	      ['推荐',''],
	      ['童装','dress'],
	      ['童鞋','shoes'],
	      ['婴童用品','daily_goods'],
	      ['女装','woman_dress'],
	      ['鞋包','woman_shoes_bags'],
	      ['居家','house'],
	      ['美妆','beauty'],
	      ['美食','food'],
	      ['下期预告','']
      ],
      imgItem:[],
      loading:false,
      list:[],
      index:0,
      headline:'',
      url:['new/','-1'],
      cb:'BeibeiMartshowNewGet',//jsonp的回调函数名
      loadword:''//判断点击的是否为同一个导航按钮
    }
  },

  methods:{
  	loadMore(reload){
  		this.loading=true;
  		var that=this;
  		jsonp('http://sapi.beibei.com/martshow/'+this.url[0]+ ++that.index +this.url[1]+'.html',{
			prefix:'jsonp',
			name:this.cb
			},function(err,res){
				if(!res){return}
				if(reload){//重新加载-切到其他路由-数据赋值
					that.list=res.martshows
				}else{//当前路由-数据追加
					that.list.push(...res.martshows)
				}
				if(!that.headline){
						if(res.today_tip_title){
							that.headline=res.today_tip_title+'<i></i>'+res.today_tip_subtitle;		
						}else{
							that.headline='今日特卖<i></i>每天9点准时上新';
						}
				}
			})
  	},
  	getImg(){//获取豆腐块导航
  		var that=this
	  	jsonp('http://sapi.beibei.com/resource/h5-ads-7_28_246_9_236.html',{
				prefix:'jsonp',
				name:'ads'
			},function(err,res){
				that.imgItem=res.promotion_pro_shortcuts.sort((item1,item2)=>{
					return parseInt(item2.img.split('x')[1])-parseInt(item1.img.split('x')[1])
				});
			})
  	},
  	changePage(str){//当前页面的子路由
			this.active=this.$route.params.mainid;
  		if(this.loadword!=str){
  			this.loadword=str;
  			this.imgItem=[];
  		}else{
  			return;
  		}
  		this.index=0;
  		if(str){//修改 请求接口数据及回调函数名
  			this.url=['channel/','-'+str+'---0'];
  			this.cb='BeibeiMartshowChannelGet';
  		}else{//进入默认也或者下期预告页 
  			this.url=['new/','-1'];
  			this.cb='BeibeiMartshowNewGet';
  			this.getImg()
  		}
  		this.loadMore('reload')
  	},
  	toClassify(id){//跳转到分类页
  		location.hash='classify/'+id
  	}
  },
  updated(){
  	this.loading=false;
  },
  mounted(){
  	this.getImg()
  	 new Swiper('.swiper-container', {
        slidesPerView:'auto',
        freeMode: true
    });
  },
  components:{
  	Footnav,
  	GoTop
  }  
}
</script>

<style scoped lang="scss">
 .main-box{
 	width:100%;
 	position:relative;
	margin-bottom:78px;
	overflow:hidden;
	.swiper-slide{width:auto;}
 	.top-nav{
 		padding:0 1.728rem 0 .6rem;
 		overflow:hidden;
 		position:relative;
 		background:#fff;
 	}
 	.headline{
 		height: 1.92rem;
    width: 100%;
    text-align: center;
    line-height: 1.92rem;
    color: #3d3d3d;
    font-size: 16px;
    background: #fff;
    border-bottom: 1px solid #e4e4e4;
    font-size:32px;
 	}
 	.tit-box{
    padding: .32rem .512rem;
    background-color: #fff;
    overflow: hidden;
    height: 1.36533333rem;
    span{
    	float: left;
	    font-size: 30px;
	    color: #3d3d3d;
	    margin-left: .34133333rem;
	    height: 1.36533333rem;
	    line-height: 1.36533333rem;
	    max-width: 4.48rem;
	    overflow: hidden;
	    text-overflow: ellipsis;
			white-space:nowrap;
			&:nth-of-type(2){
		    font-size: 26px;
		    color: #8f8f8f;
		    max-width: 5.54666667rem;
			}
    }
    i{
    	float:right;
    	font-size:26px;
    	color: #8f8f8f;
    	height: 1.36533333rem;
	    line-height: 1.36533333rem;
    }
 		img{
 			width: 1.36533333rem;
	    height: 1.36533333rem;
	    float: left;
	    vertical-align:middle;
 		}
 	}
 	.space-box{
 		margin-bottom:14px;
 		overflow:hidden;
 	}
 	.goods-info{
    width: 50%;
    display: inline-block;
    box-sizing: border-box;
    float: left;
    background-color: #fff;
    padding: .17066667rem .448rem .256rem .512rem;
    overflow: hidden;
    &:first-of-type{
    	border-right:1px solid #ccc;
    }
    h2,h3{
    	padding:6px 0;
    }
    h2{
    	color: #ff4965;
    	font-size: .55466667rem;
    }
    h3{
	    color: #3d3d3d;
	    font-size:.6rem;
	    line-height: .64rem;
	    height: .64rem;
	    overflow: hidden;
	    text-overflow: ellipsis;
	    white-space:nowrap;
    }
    i{
	    font-size: .55466667rem;
	    color: #999;padding-left:10px;
	    text-decoration: line-through;
    }
    span{
			font-weight:600;
    	font-size: .72533333rem;
    }
 	}
 	.item-box{
 		overflow:hidden;
 		background:#fff;
 		div{
 			border:1px solid #e4e4e4;
 			border-top:none;border-right:none;
 			box-sizing:border-box;
 			width:30%;float:left;
 			&:first-child{
 				border:none;
 				width:40%;
 			}
 			&.border-none{
 				border-bottom:none;
 			}
 		}
 	}
 	.top-nav-box{
 		white-space:nowrap;
 		
 	}
		a{
			display:inline-block;
			font-size: 28px;
	    text-align: center;
	    padding: 0 .4rem;
	    line-height: 1.70666667rem;
	    cursor: pointer;
	    border-bottom:.1rem solid transparent;
	    &.router-link-active{border-color:#ff4965;color:#ff4965;}
	    &:last-child{
	    	display:inline-block;
	    	margin-right:1.2rem;
	    }
		}
		.search{
			width:3.456rem;
			height:1.728rem;
			position:absolute;
			right:-2px;top:0;
			z-index:9999;
			.son{
				display:block;
				float:left;
		    width: 1.728rem;
		    height: 1.728rem;
background: -webkit-linear-gradient(left,transparent ,#fff );
		    &.iconfont{
		    	margin:0;
		    	padding:0;
		    	font-size: .85333333rem;
		    	text-align: center;
		    	line-height: 1.728rem;
		    	color: #000;
		    	background:#fff;
		    }
			}
		}
 }
</style>



