<template>
	<div class="boxxx">
		<ul class="list-contain" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
			<li class="items" v-for='x in list'>
				<img :src="x.rect_img"/>
				<p class="sq">{{x.title}}</p>
				<div class="ready">
					<div>
						<span>￥{{x.group_price/100}}</span>
						<span class="single">单买价￥{{x.origin_price/100}}</span>
					</div>
					<button>9点抢购></button>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
	
	import Vue from 'vue'
	import { InfiniteScroll } from 'mint-ui';
	import jsonp from 'jsonp'
	Vue.use(InfiniteScroll);
	
	export default {
		name:'son3',
		data(){
			return {
				order:1,
				list:[]
			}
		},
		methods:{
			loadMore() {
				this.getInfo(this.order)
				this.loading = true;
			},
			getInfo(num) {
				var that = this
				jsonp('http://sapi.beibei.com/item/fightgroup/'+num+'-40-today_group-tomorrow-0.html', {
					prefix: 'jsonp',
					name: 'BeibeiFightgroupItemGet'
				}, function(err, res) {					
					that.list = that.list.concat(res.fightgroup_items)
					that.loading = false
					that.order++
				})
			}
		},
		beforeMount(){
			this.getInfo(this.order)
		}
	}
</script>

<style scoped lang='scss'>
	.list-contain{
		padding-bottom: 2.77333333rem;
    	min-height: 25rem;
    	background-color: #f2f4f6;
    	overflow: hidden;
  		.items{
		    background-color: #fff;
		    margin-bottom: .34133333rem;
		    p.sq{
			    margin-top: .512rem;
			    padding: 0 .512rem;
			    font-size:30px;
		    }
		    .ready{
		    	color: #2cb951;
		    	padding: .512rem;
		    	font-size:26px;
		    	display: flex;
		    	justify-content: space-between;
		    	.single{
		    		color: #999;
				    padding-left: .256rem;
				    text-decoration: line-through;
		    	}
		    	button{
		    		background-color: #2cb951;
		    		line-height:30px;
		    		width:84px;
		    		color:#fff;
		    		border-radius: 5px;
		    	}
		    }
    	}
	}
</style>


